import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Spinner from '../commen/spiner';
import { getProfiles } from '../../action/profileAction'
import ProfileItem from './profileItem'
class Profiles extends Component {
 
    componentDidMount(){
        this.props.getProfiles()
    }
    render() { 
        const { profiles,loading } =this.props.profile;
        let profileItems;
        if(profiles==null || loading){
            profileItems=<Spinner />
        }else{
            if(profiles.length>0){
                profileItems=profiles.map(profile=>(
                    <ProfileItem key={profile._id} profile={profile} />
                ))
            }
            else{
                profileItems=<h4>Profile is not found</h4>
            }
        }
        return ( 
            <div className="profiles">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">
                            <h1 className="display-4 text-center">Devloper Profile</h1>
                            <p className="lead text-center">Browse and connect with devlopers</p>
                            {profileItems}
                        </div>
                    </div>
                </div>
            </div>
         );
    }
}

Profiles.propTypes={
    getProfiles:PropTypes.func.isRequired,
    profile:PropTypes.object.isRequired
}
 const mapStateToProps=state=>({

profile:state.profile
 })
export default connect(mapStateToProps,{getProfiles})(Profiles);